html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1;
  font-weight: normal;
  color: @text-color;
  overflow-x: hidden;

  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input::-webkit-input-placeholder {
  color: #999999;
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}
