/*         Свойства общие для всех страниц         */
html {
  line-height: 1.15;
}
body {
  margin: 0;
}
main {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
pre {
  font-family: monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline dotted;
}
b {
  font-weight: bolder;
}
strong {
  font-weight: bolder;
}
code {
  font-family: monospace;
  font-size: 1em;
}
kbd {
  font-family: monospace;
  font-size: 1em;
}
samp {
  font-family: monospace;
  font-size: 1em;
}
small {
  font-size: 80%;
}
sub {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
  bottom: -0.25em;
}
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
  top: -0.5em;
}
img {
  border-style: none;
}
button {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  overflow: visible;
  text-transform: none;
}
button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring {
  outline: 1px dotted ButtonText;
}
input {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  overflow: visible;
}
optgroup {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
select {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  text-transform: none;
}
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  overflow: auto;
}
[type="button"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
[type="button"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
[type="reset"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
[type="reset"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
progress {
  vertical-align: baseline;
}
[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button {
  height: auto;
}
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  outline-offset: -2px;
}
::-webkit-file-upload-button {
  font: inherit;
}
details {
  display: block;
}
summary {
  display: list-item;
}
template {
  display: none;
}
[hidden] {
  display: none;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1;
  font-weight: normal;
  color: #444444;
  overflow-x: hidden;
  background-color: #ffffff;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
input::-webkit-input-placeholder {
  color: #999999;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: medium;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/*         Цвета        */
/*         Основные размеры        */
/*         Параметры шрифтов         */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  min-width: 320px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 110px;
  }
}
/*         Мобильная разметка         */
.button {
  display: block;
  width: 100%;
  padding: 13px 0;
  font-family: "Oswald", sans-serif;
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  background-color: #68b738;
  transition: 0.1s;
}
.button:hover,
.button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--additional {
  color: #444444;
  background-color: #f2f2f2;
}
.button--additional:hover,
.button--additional:focus {
  background-color: #ebebeb;
}
.button--additional:active {
  color: rgba(68, 68, 68, 0.3);
}
/*         Разметка под планшет         */
@media (min-width: 768px) {
  .button {
    width: 250px;
    padding: 17px 0;
    font-size: 20px;
    line-height: 20px;
  }
}
/*         Мобильная разметка         */
.site-list__icon-comb {
  fill: red;
}
.page-header__container {
  padding-top: 15px;
}
.page-header__main-nav {
  position: relative;
}
.page-header__logo {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 203px;
  margin-bottom: 15px;
}
.page-header__logo:hover,
.page-header__logo:focus {
  opacity: 0.7;
}
.page-header__logo:active {
  opacity: 0.5;
}
.page-header__logo-image {
  display: block;
}
.page-header__toggle {
  position: absolute;
  top: -43px;
  right: 0;
  width: 25px;
  height: 20px;
  border: none;
  outline: none;
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.page-header__toggle:hover,
.page-header__toggle:focus {
  opacity: 0.7;
}
.page-header__toggle:active {
  opacity: 0.5;
}
.main-nav--closed .page-header__toggle {
  background-image: url("../img/icon-mobile-menu.svg");
}
.main-nav--opened .page-header__toggle {
  background-image: url("../img/icon-close.svg");
}
.page-header__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.page-header__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px 0;
}
.page-header__item::before {
  content: "";
  position: absolute;
  left: -20px;
  right: -20px;
  top: 0;
  height: 1px;
  background-color: #ededed;
}
.page-header__item:last-child::after {
  content: "";
  position: absolute;
  left: -20px;
  right: -20px;
  bottom: 0;
  height: 1px;
  background-color: #ededed;
}
.page-header__link {
  margin: 0;
  padding: 5px 0;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  white-space: nowrap;
  color: #111111;
}
.page-header__link:hover,
.page-header__link:focus {
  color: rgba(17, 17, 17, 0.6);
}
.page-header__link:active {
  color: rgba(17, 17, 17, 0.3);
}
.page-header__link--active {
  border-bottom: 1px solid #68b738;
}
@media (max-width: 767px) {
  .main-nav--closed .page-header__list {
    display: none;
  }
  .main-nav--nojs .page-header__list {
    display: block;
  }
  .main-nav--nojs .page-header__toggle {
    background-image: none;
  }
}
/*         Разметка под планшет         */
@media (min-width: 768px) {
  .page-header__container {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 25px;
  }
  .page-header__main-nav {
    margin-left: auto;
  }
  .page-header__logo {
    margin: 0;
    padding: 0;
  }
  .page-header__logo img {
    width: 174px;
  }
  .page-header__toggle {
    display: none;
  }
  .page-header__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
    border: none;
  }
  .page-header__item {
    margin-right: 35px;
  }
  .page-header__item::after {
    display: none;
  }
  .page-header__item::before {
    display: none;
  }
  .page-header__item:last-child {
    margin-right: 0;
  }
  .page-header__link {
    font-size: 20px;
    line-height: 24px;
    color: #111111;
  }
}
/*         Разметка под desktop        */
@media (min-width: 1440px) {
  .page-header__container {
    padding-top: 35px;
  }
  .page-header__picture img {
    width: 206px;
    height: 60px;
  }
  .page-header__link--colored {
    margin: 0;
    padding: 5px 0;
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
    color: #ffffff;
  }
  .page-header__link--colored:hover,
  .page-header__link--colored:focus {
    color: #ffffff;
    opacity: 0.6;
  }
  .page-header__link--colored:active {
    color: #ffffff;
    opacity: 0.3;
  }
  .page-header__link--active-colored {
    border-bottom: 1px solid #ffffff;
  }
  .page-header--background {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    height: 696px;
    width: 50%;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-position: 100% 0;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
