/*         checkbox-label         */
.checkbox-label {
  position: relative;
  margin-bottom: 10px;
  padding-left: 46px;
  cursor: pointer;
}
.checkbox-label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 30px;
  border: 1px solid rgba(205, 205, 205, 0.5);
  transform: translateY(-20%);
}
.checkbox-label:hover::before {
  border: 1px solid #cdcdcd;
}
/*         Разметка под планшет         */
@media (min-width: 768px) {
  .checkbox-label {
    margin-bottom: 30px;
  }
}
/*         Разметка под desktop        */
@media (min-width: 1440px) {
  .checkbox-label {
    margin-bottom: 0;
  }
}
/*         radio-label         */
.radio-label {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  margin-bottom: 12px;
  padding-left: 46px;
  cursor: pointer;
}
.radio-label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 30px;
  height: 30px;
  border: 2px solid #ededed;
  border-radius: 50%;
  transform: translateY(-50%);
}
.radio-label:hover::before {
  border-color: #cdcdcd;
}
.radio-label:active {
  opacity: 0.3;
}
/*         Разметка под desktop        */
@media (min-width: 1440px) {
  .radio-label {
    padding-left: 90px;
  }
  .radio-label::before {
    content: "";
    width: 40px;
    height: 40px;
  }
}
/*         Мобильная разметка         */
.cat-fieldset {
  position: relative;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  border: none;
}
.cat-fieldset::after {
  content: "";
  position: absolute;
  top: 13px;
  right: -20px;
  left: 20px;
  height: 3px;
  width: 0;
  background-color: #68b738;
  z-index: -1;
}
.cat-fieldset--cat-name::after {
  width: 100%;
}
.cat-fieldset--cat-comment::after {
  width: 100%;
}
.cat-fieldset--addon::after {
  width: 100%;
}
